import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'button-prototype';
  public points : number = 10;
  public parentHint : string = "";


  sendHint(input:string) {
    this.parentHint = input;
    console.log("hint sent (" + input + ")");
  }

  //emitted value does not have a use right now
  subtractPoint(emittedValue : boolean) {
    console.log("subtracting point");
    this.points--;
  }
}
